<template lang="html">
  <div class="coin-detail-list">
    <div class="coin-detail-list-item">
      <p class="time">
        <span class="time-data">{{time}}</span>
        <!-- <span class="time-hour">03:20:09</span> -->
      </p>
      <p class="info">{{remark}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    time: {
      type: String,
      default: ''
    },
    remark: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.coin-detail-list {
  width: 100%;
  .coin-detail-list-item {
    height: 1.2rem;
    padding: .05rem .22rem 0;
    color: @midleColor;
    background: @whiteColor;
    .time {
      font-size: .32rem;
      line-height: .57rem;
    }
    .info {
      font-size: .3rem;
      line-height: .55rem;
    }
  }
}
</style>



