<template lang="html">
  <div class="coin-detail-page">
    <!--  -->
    <default-head header=""></default-head>

    <coin-detail-list v-for="(item, index) in data" :key="item.index" :class="{'borderB': index !== data.length-1}" :time="item.createTime" :remark="item.remark"></coin-detail-list>
    <no-data v-if="!this.data.length"></no-data>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import noData from '@/components/common/noData'
import defaultHead from '@/components/head/defaultHead'
import coinDetailList from '@/components/coinDetail/coinDetailList'

export default {
  data () {
    return {
      data: [
        {createTime: '', remark: ''}
      ]
    }
  },
  components: { defaultHead, coinDetailList, noData },
  mounted () {
    this.getCoinList()
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    getCoinList () {
      api.getCoinDetail({userId: this.userId, pageIndex: 1, pageSize: 100}).then((response) => {
        console.log(response)
        let result = response.body
        if (result.isSuccess) {
          this.data = result.data.zhaobiList
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.coin-detail-page {
  padding-top: .88rem;


}

</style>



